import React from 'react'
import BackgroundImage from "gatsby-background-image"

const Intro = (props) => {

    if (typeof window !== `undefined`)

    window.addEventListener( "scroll", function() {
            const scrolled = window.pageYOffset
            const scrollIcon = document.querySelector(".intro__scroll-icon")

            if (scrollIcon)
                scrollIcon.style.opacity = "1" - scrolled * 0.01
        },
        {
            capture: true,
            passive: true,
        }
    )


    return (
        <BackgroundImage
            Tag="section"
            className="intro"
            fluid={props.imgSrc}
            backgroundColor={`#040e18`}
        >
            <div className="intro__title">
                <h1>Lorem ipsum dolor</h1>
                <h2>Lorem ipsum dolor sit</h2>
            </div>

            <div class="intro__scroll-icon-container">
                <div className="intro__scroll-icon"></div>
            </div>
        </BackgroundImage>
    )
}

export default Intro