import React from "react"
import { Link } from "gatsby"
import OnVisible, { setDefaultProps } from 'react-on-visible';

setDefaultProps({
    visibleClassName: 'in-viewport',
    percent: 90
});

const AboutMe = () => {
    return (
        <section id="about-me">
            <OnVisible className="about-me__content">
                <h3>About Me</h3>

                <p>
                    <span>Hi there, I'm a front end developer with a passion for constantly improving a</span> <span>website's performance across all devices. </span> <span>Not only is this good for users, and the web in general, but</span> <span>as an added bonus <a href="https://webmasters.googleblog.com/2018/01/using-page-speed-in-mobile-search.html" target="_blank" rel="noopener noreferrer">Google uses page speed as a ranking factor</a>.</span>
                </p>
                
                <p>
                    <span>My core skillset is CSS and JavaScript -</span> <span>check out my <Link to="cv">CV</Link> for a more in depth look.</span>
                </p>
             </OnVisible>
        </section>
    )
}

export default AboutMe